// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import { sync } from 'vuex-router-sync';

// ImportaÃ§Ã£o de cÃ³digo de configuraÃ§Ã£o/criaÃ§Ã£o criados.
import './config/plugins';
import './config/directives';
import './config/validations';
import './config/theme';
import store from './config/flux';
import { router } from './router';

// Importando o componente raÃ­z onde a app serÃ¡ renderizada.
import App from './App';

// SincronizaÃ§Ã£o entre rotas e vuex.
// Garante o estado da store em todas as pÃ¡ginas SPA.
sync(store, router);

new Vue({ // eslint-disable-line no-new
  router,
  store,
  el: '#app',
  template: '<App/>',
  components: { App },
}).$mount();



